import React from 'react';
import styled from 'styled-components';
import Logo from "../images/login-logo.svg";
import Cover from "../images/login-hero.svg";
import Goog from "../images/google.svg";

const Login = (props) => {
    return (
        <Container>
            <Nav>
                <a href="/home">
                    <img src={Logo} alt="logo"/>
                </a>
                <div>
                    <Join>Join now</Join>
                    <Signin>Sign in</Signin>
                </div>
            </Nav>
            <Section>
                <Hero>
                    <h1>Welcome to your porfessional community</h1>
                    <img src={Cover} alt="" />
                </Hero>
                <Form>
                    <Google>
                        <img src={Goog} alt=""/>
                        Sign In With Google
                    </Google>
                </Form>
            </Section>
        </Container>
    )
}

export default Login

const Container = styled.div`
padding: 0px;
`
const Nav = styled.nav`
max-width: 1128px;
margin: auto;
padding: 12px 0 16px;
display:flex;
align-items: center;
justify-content: space-between;
flex-wrap: nowrap;
position: relative;
& > a {
    width: 135px;
    height: 34px;
    @media (max-width: 768px) {
        padding: 0 5px;
    }
}
`
const Join = styled.a`
text-decoration: none;
padding: 10px 12px;
font-size: 16px;
border-radius: 4px;
color: rgba(0,0,0,0.6);
margin-left: 12px;
cursor: pointer;
&:hover {
    background-color: rgba(0,0,0,0.08);
    color: rgba(0,0,0,0.9);

}
`
const Signin = styled.a`
cursor: pointer;
box-shadow: inset 0 0 0 1px #0a66c2;
color: #0a66c2;
transition-duration: 167ms;
font-size: 16px;
font-weight: 600;
line-height: 40px;
padding: 10px 24px;
text-align: center;
border-radius: 24px;
background-color: rgba(0,0,0,0);
&:hover {
    background-color: rgba(112,181,249,0.15);
    color: #0a66c2;
    text-decoration: none ;
}
`
const Section = styled.div`
display: flex;
align-content: start;
min-height: 700px;
padding-bottom: 138px;
padding-top: 40px;
padding: 60px 0;
position: relative;
flex-wrap: wrap;
width:100%;
max-width: 1128px;
align-items: center;
margin: auto;
@media (max-width: 768px) {
    margin: auto;
    min-height: 0px;
}
`
const Hero = styled.div`
width: 100%;
h1 {
    width:55%;
    padding-bottom: 0;
    font-size: 56px;
    color: #2977c9;
    font-weight: 200;
    line-height: 70px;
    @media (max-width: 768px) {
        text-align: center;
        font-size: 20px;
        width: 100%;
        line-height: 2;
    }
}
img {
        /* z-index: -1; */
        width: 700px;
        height: 670px;
        position: absolute;
        bottom:-2px;
        right: -150px;
        @media (max-width: 768px) {
            top:230px;
            position: initial;
            width:initial;
            height: initial
        }
    }

`
const Form = styled.div`
margin-top: 100px;
width: 400px;
@media (max-width: 768px) {
    margin-top: 20px;
    margin: auto;

}


`
const Google = styled.button`
cursor: pointer;
display: flex;
align-items: center;
justify-content: center;
background-color: #fff;
height: 56px;
width: 100%;
border-radius: 20px;
box-shadow: inset 0 0 0 1px rgb(0 0 0 / 60%), inset 0 0 0 2px rgb(0 0 0 / 0%);
vertical-align: middle;
z-index: 0;
transition-duration: 167ms;
font-size: 20px;
color: rgba(0,0,0,0.6);
&:hover {
    background-color: rgba(207,207,207, 0.25);
    color: rgba(0,0,0,0.75)
}
`