import React from "react";
import styled from "styled-components";
import Home from "../images/home-logo.svg"; 
import Icon from "../images/search-icon.svg";
import NavHome from "../images/nav-home.svg";
import NavWork from "../images/nav-network.svg";
import NavMsg from "../images/nav-messaging.svg";
import Navjops from "../images/nav-jobs.svg";
import NavNot from "../images/nav-notifications.svg";
import UserImg from "../images/user.svg";
import Down from "../images/down-icon.svg";
import WorkImg from "../images/nav-work.svg";
const Header = (props) => {

    return (
        <Container>
            <Content>
                <Logo>
                    <a href="/home">
                        <img src={Home} alt="Home"/>
                    </a>
                </Logo>
                <Search>
                    <div>
                        <input type="text" placeholder="Search"/>
                    </div>
                    <SearchIcon>
                        <img src={Icon} alt="Search"/>
                    </SearchIcon>
                </Search>
                <Nav>
                    <NavListWrap>
                        <NavList className="active">
                            <a>
                                <img src={NavHome} alt=""/>
                                <span>Home</span>
                            </a>
                        </NavList>
                        <NavList>
                            <a>
                                <img src={NavWork} alt=""/>
                                <span>My Network</span>
                            </a>
                        </NavList>
                        <NavList>
                            <a>
                                <img src={Navjops} alt=""/>
                                <span>Jops</span>
                            </a>
                        </NavList>
                        <NavList>
                            <a>
                                <img src={NavMsg} alt=""/>
                                <span>Messaging</span>
                            </a>
                        </NavList>
                        <NavList>
                            <a>
                                <img src={NavNot} alt=""/>
                                <span>Notification</span>
                            </a>
                        </NavList>
                        <User>
                            <a>
                                <img src={UserImg} alt="" />
                                <span>Me</span>
                                <img src={Down} alt=""/>
                            </a>
                            <SignOut>
                                <a>Sign Out</a>
                            </SignOut>
                        </User>
                        <Work>
                            <a>
                                <img src={WorkImg} alt=""/>
                                <span>
                                    Work
                                    <img src={Down} alt=""/>
                                </span>
                            </a>
                        </Work>
                    </NavListWrap>
                </Nav>
            </Content>
        </Container>
    )

}
export default Header;

const Container = styled.div`
background-color: #fff;
border-bottom: 1px solid rgba(0,0,0,0.08);
left:0;
position: fixed;
padding: 0 24px;
top: 0;
width: 100vw;
z-index: 100;
`

const Content = styled.div`
display: flex;
align-items: center;
margin: 0 auto;
min-height: 100%;
max-width: 1128px;
`
const Logo = styled.span `
    margin-right: 8px;
    font-size: 0px;
`
const Search = styled.div`
opacity: 1;
flex-grow: 1;
position: relative;
& > div {
    max-width: 280px;
    input {
        border: none;
        box-shadow: none;
        background-color: #eef3f8;
        border-radius: 2px;
        color: rgba(0,0,0,0.9);
        width: 218px;
        padding: 0 8px 0 40px;
        line-height: 1.75;
        font-weight: 400;
        font-size: 14px;
        height: 34px;
        border-color: #dce6f1;
        vertical-align: text-top;
    }
}
`
const SearchIcon = styled.div`
width: 40px;
position: absolute;
z-index: 1;
top: 10px;
left: 2px;
border-radius: 0 2px 2px 0;
pointer-events: none;
display: flex;
align-items: center;
justify-content: center;
`
const Nav = styled.nav`
margin-left: auto;
display:flex;
@media (max-width: 768px) {
    position: fixed;
    left: 0;
    bottom:0;
    background: white;
    width: 100%;
}
`
const NavListWrap = styled.ul`
display:flex;
flex-wrap: nowrap;
list-style-type: none;
.active {
    span:after {
        position: absolute;
    content: "";
    transform: scaleX(1);
    border-bottom: 2px solid #fff;
    bottom: 0;
    left: 0;
    width:100%;
    border-color: rgba(0,0,0, 0.9);
    }
}
`
const NavList = styled.li`
display: flex;
align-items: center;
a {
    align-items: center;
    background: transparent;
    display: flex;
    flex-direction: column;
    font-size: 12px;
    font-weight: 400;
    justify-content: center;
    line-height: 1.5;
    min-height: 52px;
    min-width: 80px;
    position: relative;
    text-decoration: none;
    span {
        color: rgba(0,0,0,0.6);
        display: flex;
        align-items: center;
    }
    @media (max-width: 768px) {
        min-width: 70px;
    } 
}
&:active,
&:hover {
    a {
        span {
            color:rgba(0,0,0,0.9);
        }
    }
}
`
const SignOut = styled.div`
position: absolute;
background: white;
top: 45px;
border-radius: 0 0 5px 5px;
width:100px;
height:40px;
font-size: 16px;
transition-duration: 176ms;
text-align:center;
display:none;
`
const User = styled(NavList)`
a > img {
    width: 24px;
    border-radius:50%;
}
span {
    display:flex;
    align-items: center;
}
&:hover {
    ${SignOut} {
        align-items:center;
        display:flex;
        justify-content: center;
    }
}
`;
const Work = styled(User)`
border-left: 2px solid rgba(0,0,0, 0.08);
`
