import React from "react";
import styled from "styled-components";
import Cover from "../images/card-bg.svg";
import PhotoImg from "../images/photo.svg";
import WidgetIcon from "../images/widget-icon.svg";
import ItemIcon from "../images/item-icon.svg";
import PlusIcon from "../images/plus-icon.svg";
const LeftSide = (props) => {

    return (
        <Container>
            <ArtCard>
                <UserInfo>
                    <CardBackground/>
                    <a>
                        <Photo/>
                        <Link>Welcome, there!</Link>
                    </a>
                    <a>
                        <AddPhotoText>Add a photo</AddPhotoText>
                    </a>
                </UserInfo>
                <Widget>
                    <a>
                        <div>
                            <span>Who viewed your profile</span>
                            <span>Views of your post</span>
                        </div>
                    </a>
                </Widget>
                <Widget>
                    <a>
                        <div>
                            <span>Connections</span>
                            <span>Grow your network </span>
                        </div>
                        <img src={WidgetIcon} alt=""/>
                    </a>
                </Widget>
                <Item>
                    <span>
                        <img src={ItemIcon} alt=""/>
                        My Items
                    </span>
                </Item>
            </ArtCard>
            <ComunityCard>
                <a>
                    <span>Groups</span>
                </a>
                <a>
                    <span>
                        Events
                        <img src={PlusIcon} alt=""/>
                    </span>
                </a>
                <a>
                    Follow Hashtags
                </a>
                <a>
                    <span>Discover more</span>
                </a>
            </ComunityCard>
        </Container>
        )

}

const Container = styled.div`
grid-area: leftside;
`
const ArtCard = styled.div`
text-align: center;
overflow: hidden;
margin-bottom: 8px;
background-color: #fff;
border-radius: 5px;
transition: box-shadow 83ms;
position: relative;
border:none;
box-shadow: 0 0 0 1px rgb(0 0 0 / 15%), 0 0 0 rgb(0 0 0 / 20%);
`
const UserInfo = styled.div`
border-bottom: 1px solid rgba(0, 0, 0, 0.15);
padding: 12px 12px 16px;
word-wrap: break-word;
word-break: break-word;
`

const CardBackground = styled.div`
background: url(${Cover});
background-position: center;
height:54px;
background-size: 463px;
margin: -12px -12px 0;
`
const Photo = styled.div`
box-shadow: none;
width: 72px;
height:72px;
background-image: url(${PhotoImg});
box-sizing: border-box;
background-clip: content-box;
background-color: white;
background-position: center;
background-size: 60%;
background-repeat: no-repeat;
border:2px solid white;
margin: -38px auto 12px;
border-radius:50%;
cursor: pointer;

`
const Link = styled.div`
font-size: 16px;
line-height: 1.5;
color: rgba(0,0,0,0.9);
font-weight: 600;
`
const AddPhotoText = styled.div`
color: #0a66c2;
margin-top:4px;
font-size: 12px;
line-height: 1.33;
cursor: pointer;
font-weight: 500;
`
const Widget = styled.div`
border-bottom: 1px solid rgba(0,0,0, 0.15);
padding-top: 12px;
padding-bottom: 12px;
& > a {
    display: flex;
    justify-content: space-between;
    text-decoration: none;
    align-items: center;
    padding: 4px 12px;
    cursor: pointer;
    &:hover {
        background-color: rgba(0,0,0, 0.08);
    }
    div {
        display: flex;
        flex-direction: column;
        text-align: left;
        span{
            font-size: 12px;
            line-height: 1.33;
            &:first-child{
                color: rgba(0,0,0, 0.6);
            }
            &:nth-child(2) {
                color: rgba(0,0,0, 1)
            }
        }
    }
}
`

const Item = styled.div`
border-color: rgba(0,0,0, 0.8);
text-align: left;
padding: 12px;
font-size: 12px;
display: block;
cursor: pointer;
span{
    display: flex;
    align-items: center;
    color: rgba(0,0,0, 1)
}
&:hover {
        background-color: rgba(0,0,0, 0.08);
    }
`
const ComunityCard = styled(ArtCard)`
padding: 9px 0 0;
text-align: left;
display:flex;
flex-direction: column;
a {
    cursor: pointer;
    color:black;
    padding: 4px 12px 4px 12px;
    font-size: 13px;
    &:hover {
        color: #0a66c2;
    }
    span {
        display:flex;
        align-items:center;
        justify-content:space-between;
    }
    &:last-child {
        color:rgba(0,0,0,0.6);
        text-decoration:none;
        border-top:1px solid #b6cac2;
        padding:12px;
        &:hover {
            background-color: rgba(0,0,0, 0.08)
        }
    }

}
`

export default LeftSide;