import React from "react";
import styled from "styled-components";
import User from "../images/user.svg";
import PhotoIcon from "../images/photo.svg";
import VideoIcon from "../images/video.png";
import EventIcon from "../images/event.jfif";
import ArticleIcon from "../images/article.png";
import Ellibse from "../images/1200px-OOjs_UI_icon_ellipsis.svg.png";
import SaleryImg from "../images/salery.jfif";
import Emogy1 from "../images/emogy1.svg";
import Emogy2 from "../images/emogy2.svg";
import Emogy3 from "../images/emogy3.svg";
import LikeIcon from "../images/like.svg";
import CommentIcon from "../images/comment.jfif";
import ShareIcon from "../images/share.png";
import SendIcon from "../images/send.png";
import CertiImg from "../images/e74041cc21 (1).jpg";
import AbdullahImg from "../images/Abdullah.jpg"
const Main = (props) => {

    return (
        <Container>
            <ShareBox>
            <div>
                <img src={User} alt=""/>
                <button>Start a post</button>
            </div>
            
            <div>
                <button>
                    <img src={PhotoIcon} alt=""/>
                    <span>Photo</span>
                </button>
                <button>
                    <img src={VideoIcon} alt=""/>
                    <span>Video</span>
                </button>
                <button>
                    <img src={EventIcon} alt=""/>
                    <span>Event</span>
                </button>
                <button>
                    <img src={ArticleIcon} alt=""/>
                    <span>Write article</span>
                </button>
            </div>
        </ShareBox>
        <div>
            <Article>
                <SharedActor>
                    <a>
                        <img src={User} alt=""/>
                        <div>
                            <span>User@user</span>
                            <span>Front-End.dev</span>
                            <span>23m.</span>
                        </div>
                    </a>
                    <button>
                        <img src={Ellibse} alt=""/>
                    </button>
                </SharedActor>
                <Description>Should Salary be !?</Description>
                <SharedImg>
                    <a>
                        <img src={SaleryImg} alt=""/>
                    </a>
                </SharedImg>
                <SocialCount>
                    <li>
                        <button>
                            <img src={Emogy1} alt=""/>
                            <img src={Emogy2} alt=""/>
                            <img src={Emogy3} alt=""/>
                            <span>76</span>
                        </button>
                    </li>
                    <li>
                        <a>3 comments</a>
                    </li>
                </SocialCount>
                <SocialAction>
                <button>
                    <img src={LikeIcon} alt=""/>
                    <span>like</span>
                </button>
                <button>
                    <img src={CommentIcon} alt=""/>
                    <span>comment</span>
                </button>
                <button>
                    <img src={ShareIcon} alt=""/>
                    <span>share</span>
                </button>
                <button>
                    <img src={SendIcon} alt=""/>
                    <span>send</span>
                </button>
                </SocialAction>
            </Article>
        </div>

        <div>
            <Article>
                <SharedActor>
                    <a>
                        <img src={User} alt=""/>
                        <div>
                            <span>User@user</span>
                            <span>Front-End.dev</span>
                            <span>23m.</span>
                        </div>
                    </a>
                    <button>
                        <img src={Ellibse} alt=""/>
                    </button>
                </SharedActor>
                <Description></Description>
                <SharedImg>
                    <a>
                        <p>Hello Every one!</p>
                    </a>
                </SharedImg>
                <SocialCount>
                    <li>
                        <button>
                            <img src={Emogy1} alt=""/>
                            <img src={Emogy2} alt=""/>
                            <img src={Emogy3} alt=""/>
                            <span>76</span>
                        </button>
                    </li>
                    <li>
                        <a>3 comments</a>
                    </li>
                </SocialCount>
                <SocialAction>
                <button>
                    <img src={LikeIcon} alt=""/>
                    <span>like</span>
                </button>
                <button>
                    <img src={CommentIcon} alt=""/>
                    <span>comment</span>
                </button>
                <button>
                    <img src={ShareIcon} alt=""/>
                    <span>share</span>
                </button>
                <button>
                    <img src={SendIcon} alt=""/>
                    <span>send</span>
                </button>
                </SocialAction>
            </Article>
        </div>

        <div>
            <Article>
                <SharedActor>
                    <a>
                        <img src={AbdullahImg} alt=""/>
                        <div>
                            <span>Abdullah Ahmed</span>
                            <span>Front-End.dev</span>
                            <span>22m.</span>
                        </div>
                    </a>
                    <button>
                        <img src={Ellibse} alt=""/>
                    </button>
                </SharedActor>
                <Description>Finally i finished github cousre at @itrax-academy</Description>
                <SharedImg>
                    <a>
                        <img src={CertiImg} alt=""/>
                    </a>
                </SharedImg>
                <SocialCount>
                    <li>
                        <button>
                            <img src={Emogy1} alt=""/>
                            <img src={Emogy2} alt=""/>
                            <img src={Emogy3} alt=""/>
                            <span>76</span>
                        </button>
                    </li>
                    <li>
                        <a>3 comments</a>
                    </li>
                </SocialCount>
                <SocialAction>
                <button>
                    <img src={LikeIcon} alt=""/>
                    <span>like</span>
                </button>
                <button>
                    <img src={CommentIcon} alt=""/>
                    <span>comment</span>
                </button>
                <button>
                    <img src={ShareIcon} alt=""/>
                    <span>share</span>
                </button>
                <button>
                    <img src={SendIcon} alt=""/>
                    <span>send</span>
                </button>
                </SocialAction>
            </Article>
        </div>

        </Container>
        )

}

const Container = styled.div`
grid-area: main;
`
const CommonCard = styled.div `
text-align: center;
overflow: hidden;
margin-bottom: 8px;
background-color: #fff;
position: relative;
border-radius: 5px;
border: none;
box-shadow: 0 0 0 1px rgb(0 0 0 /15%), 0 0 0 rgb(0 0 0 /20%);
`
const ShareBox = styled(CommonCard)`
display:flex;
flex-direction: column;
color: 0 0 8px;
background: white;
margin:0 0 8px;
div {
    button {
        outline:none;
        color: rgba(0,0,0, 0.6);
        font-size: 14px;
        line-height: 1.5;
        min-height:48px;
        background: transparent;
        border:none;
        display:flex;
        align-items: center;
        font-weight: 600;
    }
    &:first-child {
        display:flex;
        align-items:center;
        padding: 8px 16px 0px 16px;
        img {
            width:48px;
            border-radius:50%;
            margin-right:8px;
        }
        button {
            margin: 4px 0;
            flex-grow:1;
            padding-left: 18px;
            border: 1px solid rgba(0,0,0, 0.15);
            border-radius: 35px;
            background-color: white;
            text-align:left;
        }
    }
    &:nth-child(2) {
        display:flex;
        flex-wrap:wrap;
        justify-content: space-between;
        padding-bottom:4px;
        button {
            img{
                width:28px;
                margin:0 4px 0 -2px;
                color: #70b5f9;
            }
            span {
                color: #70b5f9;
            }
        }
    }
}
`
const Article = styled(CommonCard)`
padding: 0;
margin: 0 0 8px;
overflow: visible;
`
const SharedActor = styled.div`
padding-right: 48px;
flex-wrap: nowrap;
padding: 12px 16px 0;
margin-bottom: 8px;
align-items:center;
display: flex;
a {
    margin-right: 12px;
    flex-grow: 1;
    overflow: hidden;
    display:flex;
    text-decoration: none;
    img {
        width:48px;
        height:48px;
    }
    & > div {
        display:flex;
        flex-direction:column;
        flex-grow: 1;
        flex-basis: 0;
        margin-left: 8px;
        overflow:hidden;
        span {
            text-align:left;
            &:first-child {
                font-size: 14px;
                font-weight: bold;
                color: rgba(0,0,0, 1)
            }
            &:nth-child(n + 1) {
                font-size: 12px;
                color: rgba(0,0,0,.6)
            }
        }
    }
}
button {
    position: absolute;
    right: 12px;
    top:0;
    background:transparent;
    border: none;
    outline:none;
    img {
        width: 18px;
    } 
}
`
const Description = styled.div`
padding: 0 16px;
overflow:hidden;
color: rgba(0,0,0,0.9);
font-size: 14px;
text-align: left;
`
const SharedImg = styled.div`
margin-top:8px;
width:100%;
display:block;
position:relative;
background-color: #f9fafb;
img {
    object-fit:cover;
    width:100%;
    height:100%;
}
P {
    text-align:left;
    padding: 12px 16px 0;
}
`
const SocialCount = styled.ul`
line-height: 1.2;
display: flex;
align-items: flex-start;
justify-content: space-between;
overflow: auto;
margin: 0 16px;
padding: 8px 0;
list-style:none;
border-bottom: 1px solid #e9e5df;
li {
    margin-right: 5px;
    font-size: 12px;
    button {
        display:flex;
        border:none;
        background: transparent;
    }
}
`
const SocialAction = styled.div`
align-items:center;
display:flex;
justify-content: space-between;
margin:0;
min-height: 40px;
padding:4px 8px;

button {
    display:flex;
    justify-content: space-between;
    align-items:center;
    padding: 8px;
    color: #0a66c2;
    border:none;
    background: transparent;
    @media (max-width: 768px) {
        margin-left: 8px;
    }
    img {
        width:28px;
    }
}
`
export default Main;