import React from "react";
import styled from "styled-components";
import FeedIcon from "../images/feed-icon.svg";
import RightIcon from "../images/right-icon.svg";
import CardImg from "../images/download.png";
const RightSide = (props) => {

    return (
        <Container>
            <FollowCard>
                <Title>
                    <h2>Add to you feed</h2>
                    <img src={FeedIcon} alt=""/>
                </Title>
                <FeedList>
                    <li>
                        <a>
                            <Avatar/>
                        </a>
                        <div>
                            <span>#Linkedin</span>
                            <button>Follow</button>
                        </div>
                    </li>
                    <li>
                        <a>
                            <Avatar/>
                        </a>
                        <div>
                            <span>#Video</span>
                            <button>Follow</button>
                        </div>
                    </li>
                </FeedList>
                <Recommendation>View all recommendation
                                <img src={RightIcon} alt=""/>
                </Recommendation>
            </FollowCard>
            <BannerCard>
                <img src={CardImg} alt=""/>
            </BannerCard>
        </Container>
        )

}

const Container = styled.div`
grid-area: Rightside;
`
const FollowCard = styled.div`
text-align: center;
overflow: hidden;
margin-bottom: 59px; 
/* margin-bottom: 15px;  */
background-color: #fff;
border-radius: 5px;
position:relative;
padding: 12px;
box-shadow: 0 0 0 1px rgba(0 0 0 / 15%), 0 0 0 rgb(0 0 0 / 20%);

`
const Title = styled.div`
display: inline-flex;
align-items:center;
justify-content: space-between;
width:100%;
color:rgba(0,0,0,0.6);
`
const FeedList = styled.ul `
margin-top: 16px;
li {
    display:flex;
    align-items: center;
    margin:12px 0;
    font-size: 14px;
    position:relative;
    & > div {
        display:flex;
        flex-direction: column;
    }
    button {
        background-color: transparent;
        color:rgba(0,0,0,0.6);
        box-shadow: inset 0 0 0 1px rgba(0,0,0, 0.6);
        padding: 16px;
        align-items: center;
        border-radius: 15px;
        box-sizing:border-box;
        font-weight:600;
        display:inline-flex;
        justify-content: center;
        max-height: 32px;
        max-width: 488px;
        text-align:center;
        outline:none;
    }
}
`
const Avatar = styled.div`
margin-right:8px;
`
const Recommendation = styled.a`
color: #0a66c2;
display:flex;
align-items:center;
font-size:14px;

`

const BannerCard = styled(FollowCard)`
img{
    width:100%;
    height:100%;
}
`;
export default RightSide;